/*_________ HEADER __________*/

#header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background-color: rgba(31, 31, 31, 0.96);
  z-index: 1000;
}

.topheader {
  padding-top: 12px;
  padding-bottom: 8px;
  background-color: #09b3e5;
  color: #fff;
}

.topheader a {
  color: inherit;
}

.topheader_right {
  float: right;
  display: flex;
  align-items: center;
}

.topheader_right a {
  font-family: "ProximaNovaRegular", sans-serif;
  transition: all .2s ease;
  &:hover {
    color: #3a3a3a;
    text-decoration: none;
  }
}

.topheader_right .separator {
  font-size: 12px;
}

.topheader_right .phone {
  display: flex;
  align-items: center;
  font-size: 24px;
  margin-right: 50px;
}

.topheader_right .phone .fa {
  margin-right: 8px;
}

.topheader_right .login-block {
  margin-bottom: 5px;
}

.topheader_register {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.topheader_hire {
  font-family: "ProximaNovaRegular", sans-serif;
  font-size: 14px;
  font-weight: 300;
}

#header #nav {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  width: 100%;
  max-width: 1100px;
}

#header #nav .phone {
  padding: 0px 0px 10px 0px;
}

#header #nav .phone a {
  font-family: "ProximaNovaRegular", sans-serif;
  color: #ffffff;
  font-size: 14px;
  text-decoration: none;
  color: #ffffff;
  text-transform: uppercase;
  -webkit-transition: all 0.4s;
  transition: all 0.4s;
}

#header #nav .phone a i {
  margin-right: 10px;
}

#header #nav .logo {
  text-align: center;
  padding: 10px 0px;
}

#header #nav .logo img {
  margin-left: -25px;
}

#header #nav .headerBtn {
  margin-bottom: 5px;
}

#header #nav .menu {
  list-style: none;
  padding: 0px;
  margin: 0px;
  text-align: justify;
  text-align-last: justify;
  padding: 24px 0px;
}

#header #nav .menu>li {
  vertical-align: middle;
  color: #ffffff;
  display: inline-block;
  -webkit-transition: all 0.4s;
  transition: all 0.4s;
}

#content {
  padding-top: 115px;
}

#header #nav .menu>li>a {
  font-family: "ProximaNovaRegular", sans-serif;
  color: #ffffff;
  font-size: 14px;
  text-decoration: none;
  color: #ffffff;
  text-transform: uppercase;
  -webkit-transition: all 0.4s;
  transition: all 0.4s;
}

#header #nav .menu .menu_item_has_children>a {
  -webkit-transition: all 0.4s;
  transition: all 0.4s;
  position: relative;
}

#header #nav .menu .menu_item_has_children>a:after {
  content: '';
  display: inline-block;
  width: 12px;
  display: block;
  height: 7px;
  position: absolute;
  left: 50%;
  margin-left: -6px;
  background: url(../img/arrow_drop_dowm_menu.png) no-repeat 0 0;
}

#header #nav .menu .menu_item_has_children>a:hover:after {
  background: url(../img/arrow_drop_dowm_menu_hover.png) no-repeat 0 0;
}

#header #nav .menu>li:hover>a {
  color: #90c800;
}

.login-block a {
  font-family: "ProximaNovaRegular", sans-serif;
  color: #ffffff;
  font-size: 14px;
  text-decoration: none;
  color: #ffffff;
  text-transform: lowercase;
  -webkit-transition: all 0.4s;
  transition: all 0.4s;
}

.login-block form {
  display: inline-block;
}

.login-block button {
  font-family: "ProximaNovaRegular", sans-serif;
  color: #ffffff;
  font-size: 10px;
  text-decoration: none;
  color: #ffffff;
  text-transform: uppercase;
  -webkit-transition: all 0.4s;
  transition: all 0.4s;
  border: none;
  background: none;
}

.sliderInset {
  position: absolute;
  width: 100%;
  left: 0;
  top: 0;
}

#header #nav .menu>li:hover>.sub_menu {
  -webkit-transition-property: opacity, visibility;
  transition-property: opacity, visibility;
  -webkit-transition-duration: 0.4s, 0.4s;
  transition-duration: 0.4s, 0.4s;
  -webkit-transition-delay: 0.4s, 0.4s;
  transition-delay: 0.4s, 0.4s;
  display: block;
  visibility: visible;
  opacity: 1;
  margin-top: 0px;
}

#header #nav .sub_menu {
  display: block;
  visibility: hidden;
  opacity: 0;
  -webkit-transition-property: opacity, visibility;
  transition-property: opacity, visibility;
  -webkit-transition-duration: 0.4s, 0.4s;
  transition-duration: 0.4s, 0.4s;
  -webkit-transition-delay: 0.4s, 0.4s;
  transition-delay: 0.4s, 0.4s;
  list-style: none;
  padding: 0px;
  margin: 0px;
  position: absolute;
  top: 100%;
  width: 180px;
}

#header #nav .sub_menu li {
  background-color: rgba(0, 0, 0, 0.6);
  width: 100%;
  text-align: left;
  border-top: 4px solid rgba(121, 120, 114, 0.4);
}

#header #nav .sub_menu li:first-child {
  border-top: none;
}

#header #nav .sub_menu li a {
  font-family: "ProximaNovaExtrabld", sans-serif;
  color: #ffffff;
  display: block;
  font-size: 16px;
  padding: 2px 0px 2px 26px;
  text-decoration: none;
  -webkit-transition: all 0.4s;
  transition: all 0.4s;
}

#header #nav .sub_menu li a:hover {
  background-color: rgba(144, 200, 0, 0.76);
}


/*_________ HEADER END __________*/


