  @media(max-width: 640px) {
    .mobile_nav_button .headerBtn {
      display: none;
    }
    .partners-grid .partners-row {
      display: block;
      padding: 25px 0;
    }
    .partners-grid .partners-img {
      display: block;
      min-height: initial;
      width: 100%;
      margin: 0 auto 15px;
      border-right: none;
    }
    .partners-grid .partners-img img {
      max-height: 80px;
    }
    .partners-grid .partners-description {
      margin: 0 0 20px;
    }
    .partners-grid .partners-action {
      text-align: center;
    }
  }
}

